.policy-card{
    @include flex(center, flex-start);

    padding: 20px;
    padding-left: 0;
    box-shadow: $box-shadow;
    transition: transform .3s ease;

    &:hover{
        transform: translateY(-20px);
    }

    &__icon{
        @include flex(center, center);
        width: 40%;
        color: $main-color;
        font-size: 3rem;
    }

    &__info{
        &__name{
            font-size: 1.25rem;
            font-weight: 600;
            margin-bottom: 10px;
        }

        &__description{
            font-size: 1rem;
            color: $text-second-color;
        }
    }


}