.catalog{
    @include flex(flex-start, flex-start);

    @include tablet{

        flex-direction: column;
    }
       
    

    &__filter{
        width: 20%;

        @include tablet{
            width: max-content;
            height: 100vh;
            background-color: $main-bg;
            padding: 1rem;
            position: fixed;
            left: 0;
            top: $header-tablet-height;
            z-index: 100;
            box-shadow: $box-shadow;
            transform: translateX(-100%);
            transition: transform .3s ease;
        }

        @include mobile{
            top: $header-mobile-height;

        }

        &.active{
            transform: translate(0);
        }
        &__widget{
            margin-bottom: 20px;

            &__title{
                font-size: 1.25rem;
                font-weight: 600;
                margin-bottom: 16px;
                text-transform: capitalize;
            }

            &__content{
                color: $text-second-color;

                &__item{
                    margin-bottom: 13px;
                }
            }
        }

        &__close{
            display: none;

            @include tablet{
                display: block;
                font-size: 1.5rem;
                cursor: pointer;
                margin-bottom: 20px;
            }
            
        }


    }

    &__content{
        flex-grow: 1;

        @include tablet{
            width: 100%;
        }
    }


    &__filter__toggle{
         display: none;

         @include tablet{
             display: block;
             margin-bottom: 2rem;
         }
    }
}